.navbar {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), #0f3d6e;
  height: 64px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 70px;
  position: relative;
}
.navbar__logo {
  width: 170px;
  margin-top: 100px;
}

.navbar__links {
  list-style: none;
}
.navbar__links li {
  display: inline-block;
  padding: 0 15px;
  color: white;
}
.navbar__links li a {
  transition: all 0.3s ease 0s;
  margin-right: auto;
  color: white;
}
.navbar__links li a:hover {
  color: red;
}

.cta {
  margin-right: -41px;
}

button {
  padding: 9px 25px;
  background-color: red;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}
button:hover {
  background-color: aqua;
}

.navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;
}
.navbar-menu svg {
  cursor: pointer;
}
.navbar-menu__container {
  background-color: red;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  text-align: end;
  padding: 2rem;
  position: absolute;
  top: 40px;
  right: 0;
  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0 0 5 black;
}

@media screen and (max-width: 767px) {
  .pc {
    display: none;
  }
  .navbar-menu {
    display: flex;
  }
}
.colorsaumon {
  color: #e47272;
}

.colorsaumongris {
  color: #ffcdcd;
}

.Prestation {
  display: flex;
  flex-direction: column;
  background: #1277ba;
  padding-bottom: 40px;
}
.Prestation__titlePrestation {
  color: #ffffff;
  font-family: "Open Sans";
  font-weight: 700;
  font-size: 71px;
  line-height: 97px;
  margin: 45px auto;
}
.Prestation__container {
  width: 100%;
  display: flex;
  justify-content: center;
}
.Prestation__container__bloc {
  display: flex;
  margin: 0px 30px 30px 30px;
}

.Prestation__container__bloc {
  padding: 10px 17px 0 38px;
  padding-bottom: 20px;
  border: 3px solid rgba(255, 255, 255, 0.47);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  width: 600px;
}
.Prestation__container__bloc h2 {
  display: block;
  width: 266px;
}
.Prestation__container__bloc__inside {
  display: flex;
}
.Prestation__container__bloc__inside p {
  line-height: 17px;
  text-align: justify;
  color: #000000;
}
.Prestation__container__bloc__inside button {
  margin-top: 30px;
  background-color: #0075ff; /* Green */
  border: none;
  color: white;
  padding: 11px 13px;
  text-align: center;
  font-size: 16px;
  box-shadow: 3px 5px 10px 1px rgba(0, 0, 0, 0.47);
  border-radius: 8px;
}
.Prestation__container__bloc__inside img {
  margin-left: auto;
  padding-left: 20px;
  width: auto;
  height: 148px;
}

.bloc1 {
  background: #76baef;
}

.bloc2 {
  background: #ffdadb;
}

@media screen and (max-width: 1130px) {
  .Prestation__titlePrestation {
    font-size: 54px;
  }
  .Prestation__container__bloc {
    margin: 0px 15px 26px 15px;
  }
  .Prestation__container__bloc__inside img {
    height: 124px;
  }
  .Prestation__container__bloc__inside p {
    font-size: 14px;
  }
  .Prestation__container__bloc__inside button {
    font-size: 13px;
  }
  .Prestation__container__bloc h2 {
    font-size: 19px;
  }
}
@media screen and (max-width: 950px) {
  .Prestation__titlePrestation {
    font-size: 44px;
  }
  .Prestation__container__bloc {
    padding: 10px 12px 0px 18px;
  }
  .Prestation__container__bloc__inside p {
    line-height: 14px;
    font-size: 12px;
  }
  .Prestation__container__bloc__inside button {
    margin-top: 13px;
    margin-bottom: 13px;
  }
  .Prestation__container__bloc__inside img {
    height: 100px;
  }
}
@media screen and (max-width: 767px) {
  .Prestation__container {
    flex-direction: column;
    align-items: center;
  }
  .Prestation__container__bloc {
    padding: 10px 12px 0px 18px;
    width: auto;
  }
  .Prestation__container__bloc__inside p {
    line-height: 14px;
    font-size: 12px;
  }
  .Prestation__container__bloc__inside button {
    margin-top: 13px;
    margin-bottom: 13px;
  }
  .Prestation__container__bloc__inside img {
    margin-top: -20px;
    height: 100px;
  }
}
.creation {
  background-color: #d7dfe4;
  padding-bottom: 50px;
  margin: -20px auto auto auto;
}
.creation h1 {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 71px;
  line-height: 97px;
  /* linear */
  background: linear-gradient(90deg, #184f6f 0%, rgba(255, 101, 0, 0.99) 100%);
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}

.GridCreation {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 24px;
  gap: 24px;
  margin: 70px 180px;
}
.GridCreation div {
  box-sizing: border-box;
  width: 100%;
  height: 372px;
  background: #1e7ee2;
  background: #1e7ee2;
  border: 3px solid rgba(255, 255, 255, 0.47);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
}

/* Footer */
.Footer {
  background: #D9D9D9;
}

.Footer_H3 {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 39px;
  line-height: 53px;
  text-align: justify;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(90deg, #184F6F 0%, rgba(255, 101, 0, 0.99) 101.58%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.Footer_Bloc_presta {
  display: flex;
  margin: auto;
  justify-content: center;
}

.Footer_presta {
  padding: 5%;
  padding-top: 0%;
}

.Footer_Bloc_devis {
  position: relative;
  z-index: 1;
  margin-top: 30px;
  display: flex;
  object-fit: cover;
  background-image: url(data:image/jpeg;base64,);
  background-repeat: round;
  width: 80%;
  border-radius: 0.66667rem;
  margin: auto;
  top: -75px;
  align-items: center;
  padding-right: 44px;
}

.Footer_confier {
  font-size: 1.6rem;
  font-family: Metropolis, arial, sans-serif;
  font-style: normal;
  font-weight: 800;
  padding: 20px;
  color: white;
  letter-spacing: -1px;
  line-height: 1.2;
}

.Footer_Button_devis {
  width: 400px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3.33333rem;
  border-radius: 1.66667rem;
  font-weight: 700;
  background: #fff;
  transition: all 400ms;
  overflow: hidden;
  color: #485fc7;
  cursor: pointer;
  text-decoration: none;
}

.Footer_Button_devis:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: #485fc7;
  color: rgb(255, 217, 119);
}

.Footer_Bloc_footer {
  display: flex;
  background: #1E1E1E;
  color: white;
  margin: auto;
  justify-content: center;
}

.Footer_Bloc_Footer_p {
  -webkit-margin-end: 10%;
          margin-inline-end: 10%;
  text-align: center;
  margin: auto;
  margin-top: 1%;
  margin-bottom: 1%;
}

.Footer_li {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 21px;
  line-height: 29px;
  /* identical to box height */
  text-align: justify;
  color: #362828;
  cursor: pointer;
}

.Footer_li:hover {
  color: #485fc7;
}

.Footer_li_SiteInternet {
  list-style-image: url(data:image/png;base64,);
}

.Footer_li_Refonte {
  list-style-image: url(data:image/png;base64,);
}

.Footer_li_Agence {
  list-style-image: url(data:image/png;base64,);
}

.Footer_li_Devis {
  list-style-image: url(data:image/png;base64,);
}

.Footer_li_Enveloppe {
  list-style-image: url(data:image/png;base64,);
}

